import { CustomEventsHelper } from "../blazor/custom-events-helper";
import { textInputTagName } from "./constants";
export class FocusInEvent extends CustomEvent {
    constructor(needSelection) {
        super(FocusInEvent.eventName, {
            detail: new FocusInEventContext(needSelection),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
FocusInEvent.eventName = textInputTagName + ".focusin";
export class FocusOutEvent extends CustomEvent {
    constructor(text, tabKey) {
        super(FocusOutEvent.eventName, {
            detail: new FocusOutEventContext(text, tabKey),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
FocusOutEvent.eventName = textInputTagName + ".focusout";
export class InputKeyboardEvent extends CustomEvent {
    constructor(detail) {
        super(InputKeyboardEvent.eventName, {
            detail: detail,
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
InputKeyboardEvent.eventName = textInputTagName + ".keydown";
export class BeforeInputEvent extends CustomEvent {
    constructor(detail) {
        super(BeforeInputEvent.eventName, {
            detail: detail,
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
BeforeInputEvent.eventName = textInputTagName + ".beforeinput";
export class FocusInEventContext {
    constructor(needSelection) {
        this.NeedSelection = needSelection;
    }
}
export class FocusOutEventContext {
    constructor(text, tabKey) {
        this.Text = text;
        this.TabKey = tabKey;
    }
}
export class EditorTextChangeEventContext {
    constructor(text) {
        this.Text = text;
    }
}
export class EditorTextInputEventContext extends EditorTextChangeEventContext {
    constructor(text, version) {
        super(text);
        this.Version = version;
    }
}
export class EditorTextInputEvent extends CustomEvent {
    constructor(text, version) {
        super(EditorTextInputEvent.eventName, {
            detail: new EditorTextInputEventContext(text, version),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
EditorTextInputEvent.eventName = textInputTagName + ".textinput";
export class EditorTextChangeEvent extends CustomEvent {
    constructor(text) {
        super(EditorTextChangeEvent.eventName, {
            detail: new EditorTextChangeEventContext(text),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
EditorTextChangeEvent.eventName = textInputTagName + ".textchange";
CustomEventsHelper.register(EditorTextInputEvent.eventName, x => {
    return x.detail;
});
CustomEventsHelper.register(EditorTextChangeEvent.eventName, x => {
    return x.detail;
});
CustomEventsHelper.register(FocusInEvent.eventName, x => {
    return x.detail;
});
CustomEventsHelper.register(FocusOutEvent.eventName, x => {
    return x.detail;
});
CustomEventsHelper.register(InputKeyboardEvent.eventName, x => {
    const e = x.detail;
    return {
        "Key": e.key,
        "Code": e.code,
        "CtrlKey": e.ctrlKey,
        "AltKey": e.altKey,
        "ShiftKey": e.shiftKey,
        "MetaKey": e.metaKey
    };
});
CustomEventsHelper.register(BeforeInputEvent.eventName, x => {
    const e = x.detail;
    return {
        "InputType": e.inputType,
        "Text": e.data,
    };
});
